.Search {
    position: fixed;
    left: 0px;
    top: 0px;
    z-index: 1000;
    width: 100%;
    background-color: rgb(255, 133, 42);
}
.Search .header-bd {
    padding-right: 9px;
    padding-left: 42px;
    padding-top: 6px;
    padding-bottom: 6px;
    background-size: 27px 20px;
    background-image: url(https://bkimg.cdn.bcebos.com/pic/5fdf8db1cb134954b37001e85c4e9258d0094ad4?x-bce-process=image/resize,m_lfit,w_268,limit_1/format,f_jpg);
    background-repeat: no-repeat;
    background-position: 3px center;
}
.Search .header-bd .placeholder {
    display: block;
    font-size: 0px;
    background-color: rgb(255, 78, 34);
    text-align: center;
    border-radius: 4px;
}
a {
    text-decoration: none;
}
.Search .header-bd .placeholder .text {
    height: 25px;
    line-height: 25px;
    font-size: 13px;
    display: inline-block;
    color: rgb(240, 224, 220);
}
.Search .header-bd .placeholder .text::before {
    font-size: 15px;
    display: inline-block;
    color: rgb(255, 255, 255);
    margin-right: 5px;
}
.imgs{
    width: 100%;
    text-align: center;
}
.fenimg{
    width: 177px;
    height: 20px;
    margin:0 auto;
}


.contents{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    position: relative;
    box-sizing: border-box;
    place-content: flex-start space-between;
    overflow: hidden;
    flex-wrap: wrap;
    padding: 0 8px;
    background: #f2f2f2;
    padding-bottom: 13px;
    margin-bottom: 30px;
}
.contents li{
    border-radius: 6px;
    overflow: hidden;
    width: 45%;
    height: 287px;
    position: relative;
    background: #fff;
    margin-top: 9px;
    margin-left: 4px;
    margin-right: 4px;
    text-align: center;
}
.contents li img{
    height: 188px;
}
.contents li .title{
    margin-top: 12px;
    height: 40px;
    position: relative;
    overflow: hidden;
    line-height: 20px;
    min-height: 0;
    color: black;
    font-size: 13px;

}
.contents li .price{
    flex-direction: row;
    margin-top: 12px;
    height: 22px;
    line-height: 22px;
    color: coral;
    font-size: 18px;
}
.bottomBtn{
    position: fixed;
    bottom:0;
    width: 100%;
}
.btn{
    height: 50px;
    line-height: 50px;
    display: flex;
    text-align: center;
}
.btn button{
    border: none;
}
.btn :nth-child(1){
    background-color: white;
    width: 16%;
}
.btn :nth-child(2){
    background-color: white;
    width: 16%;

 }
.btn :nth-child(3){
   background-color: white;
   width: 16%;

}
.btn :nth-child(4){
    width: 26%;
    color: #fff;
    background-color: #FF9500;
 }
 .btn :nth-child(5){
    width: 26%;
    background: #FF0036;
    color:white;
 }
.dingbu{
    position: fixed;
    bottom: 77.28px;
    right: 11.04px;
    cursor: pointer;
    opacity: 1;
    visibility: visible;
}
.dingbu-1{
    position: relative;
    color: rgb(187, 187, 187);
    background-color: rgb(255, 255, 255);
    line-height: 60px;
    width: 44px;
    height: 44px;
    font-size: 12px;
    text-align: center;
    border-width: 1px;
    border-style: solid;
    border-color: rgb(204, 204, 204);
    border-image: initial;
    border-radius: 50%;
}



